.fp {
  width: 100%;
  max-width: 1024px;
  display: flex;
  justify-content: space-between;
  gap: 20px;
}

.fpItem {
  flex: 1;
  gap: 10px;
  display: flex;
  flex-direction: column;
}

.fpImg {
  width: 100%;
}

.fpItem > span {
  height: 20px;
}

.fpRating > button {
  border: none;
  background-color: darkcyan;
  padding: 2px;
  color: white;
}

.fpRating > span {
  margin-left: 10px;
}
@media (min-width: 768px) {
  .fp-scroll {
    display: flex;
    width: 100%;
    justify-content: space-between;
  }
  .fpItem {
    margin: 0px 10px;
  }
}

@media (max-width: 768px) {
  .fp {
    width: 100%;
    max-width: none;
    overflow-x: scroll;
  }
  .fp-scroll {
    display: flex;
  }
  .fpItem {
    width: 220px;
    margin: 0px 10px;
  }
  ::-webkit-scrollbar {
    display: none;
  }
}
