.searchItem {
  border: 1px solid lightgray;
  padding: 10px;
  border-radius: 5px;
  display: flex;
  justify-content: space-between;
  gap: 20px;
  margin-bottom: 20px;
}

.sImage {
  width: 200px;
  height: 200px;
  object-fit: cover;
}

.sDecs {
  display: flex;
  flex-direction: column;
  gap: 10px;
  flex: 2;
}

.sTitle {
  font-size: 20px;
  color: #0071c2;
}

.sDistance {
  font-size: 12px;
}

.sTaxiOp {
  font-size: 12px;
  background-color: #008009;
  color: white;
  width: max-content;
  padding: 3px;
  border-radius: 5px;
}

.sSubtitle {
  font-size: 12px;
  font-weight: bold;
}

.sFeatures {
  font-size: 12px;
}

.sCancelOp {
  font-size: 12px;
  color: #008009;
  font-weight: bold;
}

.sCancelOpSubtitle {
  font-size: 12px;
  color: #008009;
}

.sDetail {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.sRating {
  display: flex;
  justify-content: space-between;
}

.sRating > span {
  font-weight: 500;
}
.sRating > button {
  background-color: #003580;
  color: white;
  padding: 5px;
  font-weight: bold;
  border: none;
}

.sDetailTexts {
  text-align: right;
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.sPrice {
  font-size: 24px;
}

.sTaxOp {
  font-size: 12px;
  color: gray;
}

.sCheckButton {
  background-color: #0071c2;
  color: white;
  font-weight: bold;
  padding: 10px 5px;
  border: none;
  cursor: pointer;
  border-radius: 5px;
}

@media (max-width: 768px) {
  .searchItem {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
  }
  .sCancelOpSubtitle {
    display: none;
  }
  .sPrice {
    align-self: start;
  }
  .sFeatures {
    display: none;
  }
  .sRating {
    align-self: start;
    gap: 10px;
  }
  .sTaxOp {
    align-self: start;
  }
  .sDetailTexts {
    gap: 0px;
  }
}
