.navbar {
  height: 50px;
  background-color: #003580;
  display: flex;
  justify-content: center;
}

.container {
  width: 100%;
  max-width: 1024px;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo {
  font-weight: bold;
}

.navbutton {
  margin-left: 20px;
  border: none;
  padding: 5px 10px;
  cursor: pointer;
  color: #003580;
}

@media (max-width: 768px) {
  .navbar {
    width: 100%;
  }
  .container {
    max-width: none;
    padding: 15px;
  }
}
