.mail {
  width: 100%;
  margin-top: 50px;
  background-color: #003580;
  color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  padding: 50px 0px;
}

.mailInputContainer > input {
  width: 300px;
  height: 30px;
  padding: 10px;
  border: none;
  margin-right: 10px;
  border-radius: 5px;
  outline: none;
}

.mailInputContainer > button {
  height: 50px;
  width: 100px;
  background-color: #0071c2;
  color: #fff;
  font-weight: bold;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

@media (max-width: 768px) {
  .mailInputContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .mailInputContainer > button {
    margin-top: 20px;
  }
}
