.listContainer {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.listWrapper {
  width: 100%;
  max-width: 1024px;
  display: flex;
  gap: 20px;
}

.listSearch {
  flex: 1;
  background-color: #febb02;
  padding: 10px;
  border-radius: 10px;
  position: sticky;
  top: 10px;
  height: max-content;
}

.listTItle {
  font-size: 20px;
  color: #555;
  font-weight: bold;
  margin-bottom: 10px;
}

.listResult {
  flex: 3;
}

.InputDestination {
  outline: none;
  border: none;
  height: 30px;
  font-size: 14px;
  padding: 5px;
  cursor: pointer;
}

.lsItem {
  display: flex;
  flex-direction: column;
  margin-top: 10px;
  gap: 10px;
}

.lsItem > span {
  background-color: #fff;
  height: 30px;
  padding: 5px;
  cursor: pointer;
}

@media (max-width: 768px) {
  .listWrapper {
    width: 100%;
    max-width: none;
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
  .listSearch {
    position: relative;
  }
  .lsItem {
    flex-direction: row;
  }
  .lsItem > label {
    width: 100px;
  }
  .lsItem > input {
    width: 85%;
  }
  .lsItem > span {
    width: 85%;
  }
}
