* {
  padding: 0;
  margin: 0;
}

.homeContainer {
  margin-top: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 30px;
}

.homeTitle {
  width: 1024px;
  font-size: 20px;
  font-weight: 900;
}

@media (max-width: 768px) {
  .homeTitle {
    width: 100%;
    font-size: 20px;
    font-weight: 900;
  }
}
