.hotelContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
}

.hotelWrapper {
  width: 100%;
  max-width: 1024px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  position: relative;
}

.hotelImageWrapper {
  width: 33%;
}

.bookNow {
  position: absolute;
  top: 10px;
  right: 0px;
  border: none;
  background-color: #0071c2;
  color: #fff;
  padding: 15px;
  font-weight: bold;
  border-radius: 5px;
  cursor: pointer;
}

.hotelTitle {
  font-size: 30px;
}

.hotelAdress {
  font-size: 12px;
  display: flex;
  align-items: center;
  gap: 10px;
}

.hotelImg {
  width: 100%;
  object-fit: cover;
}

.hotelImage {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.hotelDetails {
  display: flex;
  justify-content: space-between;
  gap: 20px;
  margin-top: 20px;
}

.hotelDetailsTexts {
  flex: 3;
}

.hotelDetailsPrice {
  flex: 1;
  background-color: rgb(205, 245, 251);
  padding: 20px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.hotelDetailsPrice > h1 {
  font-size: 18px;
  color: #555;
}
.hotelDetailsPrice > span {
  font-size: 14px;
}
.hotelDetailsPrice > h2 {
  font-weight: 300;
}
.hotelDetailsPrice > Button {
  border: none;
  background-color: #0071c2;
  color: #fff;
  padding: 15px;
  font-weight: bold;
  border-radius: 5px;
  cursor: pointer;
}

.hotelDesc {
  font-size: 14px;
  margin-top: 10px;
}

.slider {
  position: sticky;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  background-color: rgba(111, 111, 111, 0.453);
  z-index: 999;
  align-items: center;
}

.sliderWrapper {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.sliderImg {
  width: 80%;
  height: 80vh;
}

.close {
  position: absolute;
  top: 20px;
  right: 20px;
  font-size: 30px;
  color: lightgray;
  cursor: pointer;
}

.arrow {
  margin: 50px;
  font-size: 50px;
  color: lightgray;
  cursor: pointer;
}

@media (max-width: 768px) {
  .bookNow {
    display: none;
  }
  .hotelTitle {
    padding-left: 1rem;
  }
  .hotelAdress {
    padding-left: 1rem;
  }
  .hotelDesc {
    padding-left: 1rem;
  }
  .hotelDetails {
    flex-direction: column;
  }
  .slider {
    width: 100vw;
    height: 100vh;
    background-color: #fff;
  }
  .arrow {
    margin: auto;
  }
  .sliderImg {
    height: auto;
    width: 100%;
  }
}
