.pList {
  width: 100%;
  max-width: 1024px;
  display: flex;
  justify-content: space-between;
  gap: 20px;
}

.pListItems {
  border-radius: 10px;
  overflow: hidden;
  cursor: pointer;
}

.pListImg {
  width: 100%;
  height: 150px;
  object-fit: cover;
}

.pListTitle > h1 {
  font-size: 18px;
  text-transform: capitalize;
}

.pListTitle > h2 {
  font-size: 16px;
  font-weight: bold;
}

.pListTitle {
  margin-left: 10px;
}

@media (min-width: 768px) {
  .pList-scroll {
    display: flex;
    width: 100%;
    justify-content: space-between;
  }
}
@media (max-width: 768px) {
  .pList {
    width: 100%;
    max-width: none;
    overflow-x: scroll;
  }
  .pList-scroll {
    display: flex;
  }
  .pListItems {
    margin: 0px 10px;
    width: 220px;
  }
}
