.footer {
  width: 100%;
  width: 1024px;
  font-size: 12px;
  margin-top: 40px;
}

.fLists {
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-bottom: 50px;
}

.fList {
  list-style: none;
  padding: 0;
  font-size: 15px;
}

.fListItem {
  margin-bottom: 10px;
  color: #003580;
  cursor: pointer;
}

@media (max-width: 768px) {
  .footer {
    width: 100%;
  }
  .fLists {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
}
